import { DivisiModule } from './pengaturan/divisi/divisi.module';
import { CompanyComponent } from './company.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
    {
        path: "",
        redirectTo: "module/kantor",
        pathMatch: "full"
    },
    {
        path: "module",
        component: CompanyComponent,
        children: [

            {
                path: "kantor",
                loadChildren: () => import('./pengaturan/kantor/kantor.module').then(mo => mo.KantorModule)
            },
            {
                path: "jabatan",
                loadChildren: () => import('./pengaturan/jabatan/jabatan.module').then(mo => mo.JabatanModule)
            },
            {
                path: "pangkat",
                loadChildren: () => import('./pengaturan/pangkat/pangkat.module').then(mo => mo.PangkatModule)
            },
            {
                path: "divisi",
                loadChildren: () => import('./pengaturan/divisi/divisi.module').then(mo => mo.DivisiModule)
            },
            {
                path: "departement",
                loadChildren: () => import('./pengaturan/departement/departement.module').then(mo => mo.DepartementModule)
            },
            {
                path: "grade",
                loadChildren: () => import('./pengaturan/grade/grade.module').then(mo => mo.GradeModule)
            },
            {
                path: "pengumuman",
                loadChildren: () => import('./pengumuman/pengumuman.module').then(mo => mo.PengumumanModule)
            },
            {
                path: "lisensi",
                loadChildren: () => import('./lisensi/lisensi.module').then(mo => mo.LisensiModule)
            },
            {
                path: "legal",
                loadChildren: () => import('./legal/legal.module').then(mo => mo.LegalModule)
            },
            {
                path: "event",
                loadChildren: () => import('./event/event.module').then(mo => mo.EventModule)
            },
            {
                path: "perusahaan",
                loadChildren: () => import('./perusahaan/perusahaan.module').then(mo => mo.PerusahaanModule)
            },
            {
                path: "letters",
                loadChildren: () => import('./letters/letters.module').then(mo => mo.LettersModule)
            },

        ]
    }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CompanyRoutingModule { }
