import {
    slideFadeinUp,
    slideFadeinRight,
    zoomFadein,
    fadein,
} from './../../../layouts/App/router-animations'
import { Component, OnInit } from '@angular/core'
import { Observable } from 'rxjs'
import { Store, select } from '@ngrx/store'
import { RouterOutlet } from '@angular/router'

import * as Reducers from 'src/app/store/reducers'
import { MenuService } from 'src/app/services/menu.service'

@Component({
    selector: 'app-company',
    templateUrl: './company.component.html',
    styleUrls: ['./company.component.scss'],
    animations: [slideFadeinUp, slideFadeinRight, zoomFadein, fadein],
})
export class CompanyComponent implements OnInit {
    settings$: Observable<any>
    menuLayoutType: string
    isContentNoMaxWidth: boolean
    isAppMaxWidth: boolean
    isGrayBackground: boolean
    isSquaredBorders: boolean
    isCardShadow: boolean
    isBorderless: boolean
    isTopbarFixed: boolean
    isGrayTopbar: boolean
    routerAnimation: string

    constructor(private store: Store<any>, private menuService: MenuService) {
        this.store.pipe(select(Reducers.getSettings)).subscribe(state => {
            this.menuLayoutType = state.menuLayoutType
            this.isContentNoMaxWidth = state.isContentNoMaxWidth
            this.isAppMaxWidth = state.isAppMaxWidth
            this.isGrayBackground = state.isGrayBackground
            this.isSquaredBorders = state.isSquaredBorders
            this.isCardShadow = state.isCardShadow
            this.isBorderless = state.isBorderless
            this.isTopbarFixed = state.isTopbarFixed
            this.isGrayTopbar = state.isGrayTopbar
            this.routerAnimation = state.routerAnimation
        })
    }

    menuData: any = []
    ngOnInit(): void {
        this.menuData = [
            {
                title: 'Company',
                key: 'perusahaan',
                icon: 'fe fe-home',
                url: 'perusahaan',
            },
            {
                title: 'Settings',
                key: 'pengaturan',
                icon: 'fe fe-settings',
                count: 6,
                children: [
                    {
                        title: 'Offices',
                        key: 'kantor',
                        url: '/hr/company/module/kantor',
                    },
                    {
                        title: 'Departments',
                        key: 'departement',
                        url: '/hr/company/module/departement',
                    },
                    {
                        title: 'Divisions',
                        key: 'divisi',
                        url: '/hr/company/module/divisi',
                    },
                    {
                        title: 'Positions',
                        key: 'jabatan',
                        url: '/hr/company/module/jabatan',
                    },
                    {
                        title: 'Job Grade +',
                        key: 'pangkat',
                        url: '/hr/company/module/pangkat',
                    }
                ],
            },
            // {
            //     category: true,
            //     title: 'Components',
            // },
            {
                title: 'announcements',
                key: 'pengumuman',
                icon: 'fe fe-mic',
                url: '/hr/company/module/pengumuman',
            },
            {
                title: 'events',
                key: 'event',
                icon: 'fe fe-calendar',
                url: '/hr/company/module/event',
            },
            {
                title: 'legal Documents',
                key: 'legal',
                icon: 'fe fe-file',
                url: '/hr/company/module/legal',
            },
            {
                title: 'Certificates',
                key: 'lisensi',
                icon: 'fe fe-award',
                url: '/hr/company/module/lisensi',
            },
            //{
            //    title: 'Letters',
            //    key: 'Letters',
            //    icon: 'fe fe-award',
            //    url: '/hr/company/module/lisensi',
            //},
            {
                title: 'Company Letters',
                key: 'letters',
                icon: 'fe fe-file-text',
                url: '/hr/company/module/letters',
            },
        ]

        this.menuService.menu.next(this.menuData)
    }

    routeAnimation(outlet: RouterOutlet, animation: string) {
        if (animation === this.routerAnimation) {
            return outlet.isActivated && outlet.activatedRoute.routeConfig.path
        }
    }
}
